#container {
  width: 95%;
  margin: 3.5% auto 1.5%;
  display: flex;
  flex-wrap: wrap;
  line-height: 1.35;
}

#container h3 {
  font-family: 'Big Shoulders Display', cursive;
  font-weight: normal;
  font-size: 27px;
  letter-spacing: 2px;
  padding-bottom: 5px;
}

#add-friendship {
  width: 36%;
  padding: .5% 1.5% 3%;
  background-color: rgba(0, 0, 0, 0.9);
  color: #60B0D0;
  color: rgba(92, 176, 208, 0.85);
  border-radius: 10px;
  display: table;
  max-width: 1000px;
}

#add-friendship h3 {
  padding: 0 3%;
}

#add-friendship form {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  font-family: 'Electrolize', sans-serif;
}

#add-friendship span {
  width: 45%;
  margin: 10px 0;
  display: table;
}

#add-person {
  background: #60B0D0;
  background: rgba(92, 176, 208, 0.85);
  padding: 15px 0;
  width: 100%;
  border-radius: 20px;
  display: table;
  margin: 20px auto 0;
  font-family: 'Poiret One', sans-serif;
  font-weight: bold;
}

#add-person:before {
  content: '\f055';
  font-family: 'Font Awesome 5 Pro';
  font-weight: bold;
}

/* Delete friendships */

#friendships-delete {
  width: 64%;
  padding-left: 3.5%;
}

#friendships-delete h2 {
  font-family: 'Poiret One', sans-serif;
  font-size: 36px;
  font-weight: normal;
  text-align: center;
  padding: 25% 0;
}

.content-delete {
  max-height: 1000px;
  overflow-x: scroll;
}

.content-delete::-webkit-scrollbar, textarea::-webkit-scrollbar {
  display: none;
}

.friend-delete {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  border-radius: 20px;
  background-color: rgba(0, 0, 0, 0.9);
  padding: 1.5% 0 1.5% 3.5%;
  margin-bottom: 15px;
}

.friend-delete:last-of-type {
  margin-bottom: 0;
}

.friend-delete img {
  border-radius: 50%;
  width: 35px;
  height: 35px;
}

.friend-delete h4 {
  margin: 0;
  color: rgba(255, 255, 255, 0.85);
  font-weight: normal;
  padding-left: 2%;
  letter-spacing: 2px;
  width: 94%;
  font-size: 16px;
}

/* Friendships of user */

#friendships {
  width: 100%;
  max-width: 2000px;
}

#friendships h3 {
  margin-left: 20px;
}

#friendships h3, #friendships-delete h3 {
  border-bottom: 1px solid black;
}

#content-person {
  display: flex;
  flex-wrap: wrap;
}

#all-friendships-deleted {
  text-align: center;
  font-family: 'Poiret One', sans-serif;
  font-weight: normal;
  width: 100%;
  padding: 10% 0;
  font-size: 36px;
}

#content-person, .content-delete {
  font-family: 'Big Shoulders Display', cursive;
}

/* Card person */

.person {
  background-color: rgba(255, 255, 255, 0.65);
  box-shadow: 0 4px 9px 2px rgba(0, 0, 0, 0.25);
  color: rgba(0, 0, 0, 0.9);
  width: calc(33.33% - 20px);
  margin: 0 10px 10px;
  border-radius: 20px;
  overflow: hidden;
  max-width: 500px;
  display: table;
}

.person figure{
  position: relative;
}

.image-person {
  position: relative;
}

.image-person img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

/* Name and Img of Card person */

.name-person {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  text-align: center;
  margin-top: -75px;
  position: relative;
}

.name-person figure {
  overflow: hidden;
  display: table;
  margin: 0 auto;
  width: 125px;
  box-sizing: border-box;
}

.name-person img {
  display: block;
  width: 100%;
  border-radius: 50%;
  height: 125px;
  border: 3px solid white;
}

.name-person h4 {
  margin: 10px 0 0;
}

.name-person p {
  width: 90%;
  margin: auto;
  text-align: center;
  word-break: break-all;
  font-size: 16px;
  font-style: italic;
}

/* Information of Card person */

.date {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 85%;
  margin: auto;
  border-bottom: 1px solid rgba(0, 0, 0, 0.65);
}

.date h4 {
  width: 100%;
  margin: 20px auto;
  font-size: 16px;
}

.date:first-of-type h4 {
  margin: 40px auto 20px;
}

.date h4:last-child {
  text-align: right;
}

.information-person .date:last-of-type {
  border-bottom: none;
}

.date h4 i {
  color: inherit;
  padding: 0;
}

.date h4 span {
  font-weight: normal;
}

/* Assessment person */

.assessment {
  background-color: rgba(0, 0, 0, 0.9);
  padding: 10px 0;
  text-align: center;
}

/* Icons */

.fa-edit, .fa-check {
  right: 6%;
}

.fa-edit:hover {
  color: #60B0D0;
}

.fa-check:hover {
  color: #13B700;
}

.fa-trash {
  right: 0;
}

.fa-trash:hover {
  color: #FF1919;
}

.fa-edit, .fa-trash, .fa-check {
  top: 32%;
  position: absolute;
  color: inherit;
}

#messageForDeleteFriend, #newFriendAdded, #applyChanges{
  margin: 0;
}

#completeAll, #textareaLimit{
  margin: 0 0 20px;
  letter-spacing: 2px;
}

#messageForDeleteFriend, #applyChanges, #completeAll, #textareaLimit, #newFriendAdded{
  font-family: 'Big Shoulders Display', sans-serif;
  color: rgba(255,255,255,0.85);
  font-weight: normal;
}

#check{
  color: #12A002;
  padding: 0;
}

#check:hover{
  cursor: initial;
}

.image-person .edit-image, .name-person .edit-image{
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: black;
  display: flex;
  justify-content: center;
  align-items: center;
}

.image-person .edit-image{
  background: rgba(255, 255, 255, 0.25);
}

.name-person .edit-image{
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.75);
}

#file, #file1{
  display: none;
}

.image-person .load-image:before{
  font-size: 60px;
  margin-bottom: 120%;
  color: rgba(0,0,0,0.85);
  display: block;
}

.name-person .load-image:before{
  font-size: 30px;
  color: rgba(255,255,255,0.5);
}

.load-image:before {
  content: '\f030';
  font-family: 'Font Awesome 5 Pro';
  font-weight: bold;
}

.load-image:hover:before{
  cursor: pointer;
}

#applyChanges{
  font-size: 26px;
  font-family: 'Electrolize', sans-serif;
  color: rgba(255,255,255,0.65)
}

#apply-changes-check{
  position: relative;
}

.fa-times, #apply-changes-check{
  padding: 0;
  color: rgba(255,255,255,0.85);
  font-size: 14px;
}

@media screen and (max-width: 1185px) {
  #add-friendship {
    width: 85%;
    margin: auto;
  }
  #friendships {
    padding-left: 0;
    padding: 0 2%;
    width: 100%;
  }
  #friendships-delete {
    width: 100%;
    padding-left: 0;
  }
  .content-delete {
    max-height: 500px;
  }
}

@media screen and (max-width: 1024px) {
  #add-friendship {
    padding: .5% 3% 3%;
  }
  #friendships {
    padding: 0;
  }
  #friendships h3 {
    margin-left: 0;
  }
  .person {
    width: calc(50% - 20px);
    margin: 0 10px 20px;
    border-radius: 20px;
    overflow: hidden;
    max-width: 500px;
    display: table;
  }
}

@media screen and (max-width: 768px) {
  #add-friendship {
    width: 100%;
    padding-bottom: 35px;
  }
  .person {
    width: calc(50% - 10px);
    margin: 0 5px 20px;
  }
  .image-person img {
    height: 150px;
  }
  .name-person {
    margin-top: -50px;
  }
  .name-person figure {
    width: 100px;
  }
  .name-person img {
    height: 100px;
  }
  .fa-edit, .fa-trash, .fa-check{
    top: 25%
  }
}

@media screen and (max-width: 700px) {
  .friend-delete {
    padding: 2% 0 2% 3.5%
  }
  .friend-delete h4 {
    width: 90%;
  }
}

@media screen and (max-width: 640px) {
  #add-friendship form{
    justify-content: center;
  }
  .person{
  width: 100%;
  margin: 0 auto 20px;
  }
  #add-friendship span{
    text-align: center;
    width: 100%;
  }
}

@media screen and (max-width: 480px) {
  .friend-delete h4 {
    font-size: 15px;
  }
}

@media screen and (max-width: 381px) {
  .friend-delete img{
    margin: 5px auto 7px;
  }
  .friend-delete h4{
    padding-left: 0;
    width: 95%;
    margin: auto;
  }
}

@media screen and (max-width: 280px) {
  .friend-delete img{
   width: 12%;
   height: auto;
  }
  .name-person{
    margin-top: -17%;
  }
  .name-person figure{
    width: 30%;
  }
  .name-person img{
    height: auto;
  }
  .image-person img{
    height: auto;
  }
  .friend-delete h4, #friendships h3, #friendships-delete h3, .date h4, .name-person p, #friendships-delete h2, #all-friendships-deleted {
    font-size: inherit;
  }
}